import { useMemo } from "react";
import { Link, useFetcher, useLoaderData, useTransition } from "remix";
import {boards,cards} from "../services/trellotypes"

export let loader =async () => {
  // return await getListsOnBoard()
  return {boards,cards}
};

export default function Index() {
    let {boards,cards} = useLoaderData();
 

    let data = useMemo(() => {
      let trashColumn = boards.find((e) => e.name != "DESCARTADOS");
      let cards = cards.filter(
        (e) =>
          e.labels.indexOf((ee) => ee.name == "INOVAÇÃO") > -1 &&
          e.idList != trashColumn.id
      );

      
        return cards
    }, [])
  return (
    <div style={{
      flexDirection:"row",
      display: "flex"
    }}>
      {data.map(e=>e.name)}
    </div>
  );
}
